<template>
  <div class="role_wrapper">
    <div class="systam_right_top">
      <div>
        <span>运费账户</span>
        <span>/</span>
        <span>说明说明说明说明说明说明说明说明</span>
      </div>
      <div>EXCEL导出</div>
    </div>
    <div class="systam_content_bottom">
      <div class="systam_bottom1 clearfix">
        <el-button type="primary" class="addg">新增权限角色</el-button>
      </div>
      <div class="systam_bottom2">
        <el-table :data="tableData" border style="width: 100%;font-size: 10px;">
          <el-table-column prop="control" label="权限描述">
          </el-table-column>
          <el-table-column prop="play" label="角色名称">
          </el-table-column>
          <el-table-column prop="userlist" label="用户列表">
          </el-table-column>
          <el-table-column prop="operate" label="操作">
            <template scope="scope">
              <el-button type="text"  size="small" style="color: #3573DA;">
                修改
              </el-button>&nbsp;&nbsp;&nbsp;|
              <el-button type="text"  size="small" style="color: #3573DA;">
                权限设置
              </el-button>&nbsp;&nbsp;&nbsp;|
              <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small" style="color: #DD9B93;">
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          control:'超级管理员',
          play:'享有全部权限',
          userlist:'黄蓉,郭靖',
          operate:''
        },{
          control:'领导',
          play:'享有全部权限',
          userlist:'',
          operate:''
        },{
          control:'保险专员',
          play:'专门管理保险相关事务',
          userlist:'',
          operate:''
        }]
      }
    },
    methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      }
    }
  }
</script>
<style lang="less" scoped>
  .role_wrapper{
    width: 1413px;
    background: #FAFBFF;
    float: left;
    margin-bottom:300px;
    border-left:  1px solid #EAEAF2;
    .systam_right_top{
      width: 1314px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 90px;
      line-height: 90px;
      div:nth-of-type(1){
        span:nth-of-type(1){
          font-size: 20px;
          color: #3257a7;
          font-weight: 700;
        }
        span:nth-of-type(2){
          margin: 0 5px;
        }
        span{
          font-size: 16px;
          color: #9ba5be;
        }
      }
      div:nth-of-type(2){
        width: 100px;
				height: 40px;
				color: #417640;
				border: 1px solid #417640;
				line-height: 40px;
				text-align: center;
				float: left;
				border-radius: 5px;
				margin-left: 10px;
      }
    }
    .systam_content_bottom{
      width:1314px;
      margin:0 auto;
      border: 1px solid #EAEAF2;
      border-bottom: none;
      background-color: #fff;
      padding-bottom: 500px;
      .systam_bottom1{
        width: 1280px;
        margin:0 auto;
        margin-top: 32px;
        margin-bottom: 20px;
      }
      .systam_bottom2{
        width: 1280px;
        margin:0 auto;
      }
    }
  }
</style>



